import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"
import type { Kitten } from "@/types/database";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export function generateKittenSlug(description: string, id: string, breed: string): string {
  if (!id || id === 'default') {
    throw new Error('Valid ID is required for slug generation');
  }

  // Clean and format the breed
  const cleanBreed = breed
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/[^a-z0-9-]+/g, '')
    .trim();

  // Split description into words and take first 3 meaningful words (since we're adding breed)
  const words = description
    .toLowerCase()
    .split(/\s+/)
    .filter(word => 
      // Filter out common articles and short words
      !['a', 'an', 'the', 'and', 'or', 'but', 'in', 'on', 'at', 'to', 'for', 'of', 'with'].includes(word)
    )
    .slice(0, 3);

  // Join the words and clean the result
  const cleanDesc = words
    .join('-')
    .normalize('NFD') // Handle accented characters
    .replace(/[\u0300-\u036f]/g, '') // Remove diacritics
    .replace(/[^a-z0-9-]+/g, '') // Keep only alphanumeric and hyphens
    .replace(/^-+|-+$/g, ''); // Remove leading/trailing hyphens

  // Always use exactly 8 characters of ID, pad with zeros if needed
  const shortId = id.padEnd(8, '0').slice(0, 8);

  // Combine breed, description and ID
  return `${cleanBreed}-${cleanDesc}-${shortId}`;
}

export async function updateSitemap(kittens: Kitten[]) {
  try {
    // In development, just log what would happen
    if (import.meta.env.DEV) {
      console.log('Development mode: Would update sitemap with kittens:', kittens);
      return;
    }

    // In production, call the API endpoint
    const response = await fetch('/api/updateSitemap', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ kittens }),
    });

    if (!response.ok) {
      throw new Error(`Failed to update sitemap: ${response.statusText}`);
    }

    const result = await response.json();
    console.log('Sitemap updated successfully:', result);
  } catch (error) {
    console.error('Error updating sitemap:', error);
  }
}
